@import '../variables.scss';

[am-loader] {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
    margin: 0 auto;
    transform: scale(0.7);

    &[center] {
        position: absolute;
        transform: translateX(-50%) translateY(-50%) scale(0.7);
        top: 50%;
        left: 50%;
    }
}

[am-loader="small"] {
    transform: scale(0.45);

    &[center] {
        transform: translateX(-50%) translateY(-50%) scale(0.45);
    }
}

[am-loader="large"] {
    transform: scale(1);

    &[center] {
        transform: translateX(-50%) translateY(-50%) scale(1);
    }
}

[am-loader] div {
    animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    transform-origin: 32px 32px;
}

[am-loader] div:after {
    content: " ";
    display: block;
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: #ff0000;
    margin: -3px 0 0 -3px;
}

[am-loader] div:nth-child(1) {
    animation-delay: -0.036s;
}

[am-loader] div:nth-child(1):after {
    top: 50px;
    left: 50px;
}

[am-loader] div:nth-child(2) {
    animation-delay: -0.072s;
}

[am-loader] div:nth-child(2):after {
    top: 54px;
    left: 45px;
}

[am-loader] div:nth-child(3) {
    animation-delay: -0.108s;
}

[am-loader] div:nth-child(3):after {
    top: 57px;
    left: 39px;
}

[am-loader] div:nth-child(4) {
    animation-delay: -0.144s;
}

[am-loader] div:nth-child(4):after {
    top: 58px;
    left: 32px;
}

[am-loader] div:nth-child(5) {
    animation-delay: -0.18s;
}

[am-loader] div:nth-child(5):after {
    top: 57px;
    left: 25px;
}

[am-loader] div:nth-child(6) {
    animation-delay: -0.216s;
}

[am-loader] div:nth-child(6):after {
    top: 54px;
    left: 19px;
}

[am-loader] div:nth-child(7) {
    animation-delay: -0.252s;
}

[am-loader] div:nth-child(7):after {
    top: 50px;
    left: 14px;
}

[am-loader] div:nth-child(8) {
    animation-delay: -0.288s;
}

[am-loader] div:nth-child(8):after {
    top: 45px;
    left: 10px;
}

@keyframes lds-roller {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}