[burger-button]{display:inline-block;cursor:pointer}[burger-button] [burger-line]{width:30px;height:3px;background-color:#fff;margin:6px 0;transition:0.3s}[burger-button][active] [burger-line]:nth-child(1){-webkit-transform:rotate(-45deg) translate(-5px, 6px);transform:rotate(-45deg) translate(-5px, 6px)}[burger-button][active] [burger-line]:nth-child(2){opacity:0}[burger-button][active] [burger-line]:nth-child(3){-webkit-transform:rotate(45deg) translate(-7px, -8px);transform:rotate(45deg) translate(-7px, -8px)}
