@import 'src/scss/variables.scss';

.footer {
    margin-top: auto;
    font-size: 14px;

    &__logo {
        margin-bottom: 20px;

        a {
            margin-bottom: 10px;
        }

        p {
            color: #aaaaaa;
            max-width: 270px;
        }
    }

    &__link {
        margin-bottom: 5px;

        &:hover,
        a:hover {
            color: $color-main;
        }
    }

    &__header {
        &-item {
            margin-bottom: 10px;
        }
    }

    &__social {
        display: flex;

        a + a {
            margin-left: 10px;
        }
    }

    &__mail {
        max-width: 200px;
    }

    @media (max-width: $sm) {
        &__logo {
            margin-top: 10px;
        }
    }
}