@import 'src/scss/variables.scss';

.icons {

    &__container {
        background: #F5F2F0;
        border-radius: 3px;
        margin-bottom: 10px;
        justify-content: space-around;
        margin-bottom: 30px;
        margin-left: 0!important;
        margin-right: 0!important;
    }

    &__icon {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    &__group {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;

        [am-icon] {
            margin-bottom: 10px;
            transition: all .3s ease-in-out;
        }

        &-text {
            margin-top: auto;
            font-size: 13px;
            color: $color-black-light;
        }

        &:hover {
            cursor: pointer;

            [am-icon] {
                transform: scale(1.05);
            }
        }
    }
}

.iconCopy {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    background: #fff;
    border-top: 1px solid $color-gray-dark;
    z-index: 1;

    &__close {
        position: absolute;
        right: 10px;
        top: 5px;
        cursor: pointer;
    }
}