.nav {
    position: fixed;
    top: 0;
    left: -45px;
    bottom: 0;
    background: #2A2F3A;
    color: #fff;
    width: 100%;
    max-width: 250px;
    padding: 45px 45px 20px 0;
    transition: all .2s ease-in-out;
    transform: translateX(-205px);
    z-index: 99;

    .simplebar-track.simplebar-vertical {
        width: 9px;
        opacity: 0.8;

        .simplebar-scrollbar {
            &::before {
                background: #ccc;
            }
        }
    }

    + main {
        //margin-left: 45px;
        transition: all .3s ease-in-out;

        @media (max-width: 959px) {
            & {
                margin-left: 0;
            }
        }
    }

    &.active {
        transform: translateX(0);
        padding-right: 0;
        width: 80vw;
        left: 0;

        + main {
            margin-left: 250px;
        }

        [burger-button] {
            right: 10px;

            [burger-line] {
                background: #fff;
            }
        }

        @media (max-width: 959px) {
            + main {
                margin-left: 0;
            }
        }
    }

    &__inner {
        height: 100%;
        overflow-y: auto;
    }

    &__item {

    }

    &__link {
        color: #ccc;
        width: 100%;
        padding: 10px 20px;
        display: block;
        cursor: pointer;
        transition: all .1s ease-in-out;
        font-size: 14px;

        &:hover,
        &.active {
            background: #484C54;
            color: #fff;
        }
    }

    &__logo {
        position: absolute;
        bottom: 0;
        left: 0;
        padding: 20px;
        border-top: 1px solid #484C54;
        width: 100%;
    }

    [burger-button] {
        position: absolute;
        right: -40px;
        top: 10px;
        transition: .4s all ease-in-out;

        [burger-line] {
            transition: .4s all ease-in-out;
            background: #2A2F3A;
        }
    }
}