.copy{
    display: flex;
    border-radius: 5px;
    align-items: center;
    max-width: max-content;
    margin-top: 10px;
    position: relative;
    font-family: "Fira code", "Fira Mono", monospace;
    font-size: 14px;

    &__button {
        cursor: pointer;
    }

    &__text {
        word-break: break-word;
        color: #1990b8;
        cursor: default;
    }
}