.header {

    &__name {
        font-weight: 800;
        font-size: 38px;
        margin-bottom: 10px;
    }

    .copy {
        [am-icon] {
            opacity: 0.7;
            margin-left: 7px;
            flex-shrink: 0;
            transition: opacity .3s ease-in-out;

            &:hover{
                opacity: 1;
            }
        }

        &__text {
            word-break: break-all;
        }
    }
}