@import 'src/scss/variables.scss';

.tabs {

    & > ul {
        box-shadow: inset 0 -1px 0 0 rgba(0, 0, 0, 0.1);

        li {
            display: inline-block;
        }

        li + li {
            margin-left: 20px;
        }
    }

    &__link {
        font-size: 18px;
        padding-bottom: 10px;
        cursor: pointer;
        border-bottom: 3px solid transparent;
        white-space: nowrap;

        // &.active {
        //     color: $color-main;
        //     border-bottom: 3px solid $color-main;
        // }
    }

    &__links {
        position: relative;
        width: 100%;
        //display: flex;
        // overflow-x: auto;
        // -webkit-overflow-scrolling: touch;
        // -ms-overflow-style: -ms-autohiding-scrollbar;
    }

    &__content {
        padding: 25px 0;
        display: none;

        &.active {
            display: block;
        }
    }

    &__slider {
        height: 3px;
        background: $color-main;
        width: 0;
        transition: all .3s ease-in-out;
        position: absolute;
        bottom: 0;
    }

    @media (max-width: $sm) {
        // & > ul {
        //     li + li {
        //         margin-left: 0;
        //         display: block;
        //     }
        // }
    }
}

