.tabs {
    overflow: hidden;

    .tab {
        padding: 5px;
        width: auto;
        height: 35px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        position: relative;
        font-size: 18px;
        cursor: pointer;
        margin-right: 20px;

        &[active]::after {
            width: 100%;
        }

        &::after {
            content: "";
            display: block;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%);
            width: 0;
            height: 2px;
            background: red;
            transition: all .2s ease-in-out;
        }

        &.version-2 {
            &[active]::after {
                left: 0;
            }

            &::after {
                right: 0;
                left: auto;
                transform: none;
            }
        }
    }

    .content {
        opacity: 0;
        top: 5px;
        transition: top .3s ease-in-out, opacity .3s ease-in-out;

        &[active] {
            opacity: 1;
            top: 0;
        }
    }
}