[am-accordion-tab] {
    background: #E0E0E0;

    &::after {
        content: '';
        display: block;
        border: solid #fff;
        border-width: 2px 2px 0px 0;
        display: inline-block;
        padding: 3px;
        position: absolute;
        right: 20px;
        top: 50%;
        transition: transform 0.05s ease-in-out;
        transform: rotate(45deg) translate(-50%, -50%);
        -webkit-transform: rotate(45deg) translate(-50%, -50%);
    }

    &[active] {
        &::after {
            border-width: 0 2px 2px 0;
        }
    }
}