@import 'src/scss/variables.scss';

.grid {
    .example {
        &__preview {
            [am-grid="item"] {
                background-color: rgba(255, 0, 0, 0.05);
                border: 1px solid rgba(255, 0, 0, .05);
            }
        }
    }
}

[am-content="example"] {
    margin-bottom: 30px;
}

.example {
    &__description {
        margin: 0 0 30px;
    }

    &__header {
        margin-bottom: 20px;
    }

    &__about {
        margin-bottom: 20px;

        a {
            display: inline-block;
            color: red;
            border-bottom: 1px solid red;
            transition: filter .2s ease-in-out;

            &:hover {
                filter: brightness(0.8);
            }
        }
    }

    &__preview {
        margin-bottom: 10px;
        border: 1px solid #E0DDDB;
        border-radius: 3px;
        padding: 20px;
    }

    &__code {
        margin-bottom: 10px;

        //  
        & > div {
            padding: 5px 20px!important;
            font-family: "Fira code", "Fira Mono", monospace;
            font-size: 14px;
            background: #F5F2F0;
            border-radius: 3px;

            & textarea {
                border: 1px solid #E0DDDB!important;
                transition: all .3s ease-in-out;
            }

            & textarea:focus {
                border: 1px solid rgba($color: $color-black-light, $alpha: .3)!important;
            }

            .token.operator {
                background: #F5F2F0;
            }
        }

        .npm__react-simple-code-editor__textarea {
            padding: 5px 20px!important;
            cursor: default;
        }
    }

    table {
        th {
            font-size: 15px;
        }
        td {
            font-size: 14px;
        }

        th:nth-child(1),
        td:nth-child(1) {
            width: 15%;
        }

        th:nth-child(2),
        td:nth-child(2) {
            width: 15%;
        }

        th:nth-child(3),
        td:nth-child(3) {
            width: 30%;
        }

        th:nth-child(4),
        td:nth-child(4) {
            width: 30%;
        }

        td:nth-child(1) {color: #2f9c0a;}
        td:nth-child(2) {color: #1990b8;}
        td:nth-child(3) {color: #c92c2c;}
    }

    [am-grid="container"] + [am-grid="container"],
    [am-container-fluid] + [am-container-fluid],
    [am-container] + [am-container] {
        margin-top: 10px;
    }

    &__list {
        padding-left: 20px;
        line-break: anywhere;

        li {
            list-style: inside;
            margin-bottom: 5px;
        }

        li:last-child {
            margin-bottom: 20px;
        }
    }

    .gridAlign {
        [am-grid="container"] {
            min-height: 100px;
            background-color: rgba(255, 0, 0, 0.05);
            border: 1px solid rgba(255, 0, 0, 0.05);
        }
    }
}